.skills {
  margin-bottom: 40px;
}

.skills .container {
  display: grid;
  row-gap: 30px;
  column-gap: 30px;

  @include vp-640 {
    grid-template-columns: repeat(2, 1fr);
  }

  @include vp-768 {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 15px;
  }
}

.skills__heading {
  grid-column: 1/-1;
  margin-bottom: 0;
}

.skills__group {
  position: relative;

  @include vp-640 {
    &::before {
      position: absolute;
      width: 1px;
      height: 60%;
      top: 20%;
      right: -15px;
      background-color: $color-orange;
      content: "";
    }

    &:nth-of-type(2n)::before,
    &:last-of-type::before {
      display: none;
    }
  }

  @include vp-768 {
    &::before {
      right: -7px;
    }

    &:nth-of-type(2n)::before {
      display: block;
    }

    &:nth-of-type(3n)::before {
      display: none;
    }
  }

  &::after {
    display: block;
    clear: both;
    content: "";
  }
}

.skills__subheading {
  padding-left: 45px;
  text-align: left;
}

.skills__list {
  margin: 0;
  padding-left: 18px;
}

.skills__item::marker {
  color: $color-orange;
}

.skills__icon {
  float: right;
  width: 50px;
  height: auto;
}
