.about {
  margin-bottom: 40px;
  padding-top: 30px;
}

.about .container {
  display: grid;

  @include vp-640 {
    grid-template-columns: 240px 1fr;
    column-gap: 20px;
  }

  @include vp-768 {
    column-gap: 30px;
  }
}

.about__heading {
  font-size: 20px;
  grid-column: 1/-1;
}

.about__avatar {
  display: flex;
  flex-direction: column;
}

.about__profession,
.about__name {
  margin: 0;
  margin-bottom: 20px;
  font: 700 16px/20px $ff-default;
  text-transform: uppercase;
  text-align: center;
}

.about__profession {
  order: -1;
  color: $color-orange;

  @include vp-640 {
    order: 0;
    margin-bottom: 5px;
  }
}

.about__name {
  @include vp-640 {
    margin-bottom: 0;
  }
}

.about__image {
  margin: 0 auto 20px;
  border: 3px solid $color-accent-main;
  border-radius: 50%;
}

.about_description {
  p {
    text-indent: 15px;
  }

  p:last-child {
    margin-bottom: 0;
  }
}
