.modal-project__title {
  text-align: center;
}

.modal-project__description {
  width: 75%;
  margin-left: auto;
  font-weight: 700;
  font-size: 12px;
  font-style: italic;
  text-align: right;
}

.modal-project__divider {
  height: 2px;
  margin-top: 0;
  margin-bottom: 14px;
  border: 0;
  background: $color-gray;
}

.modal-project__tags {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.modal-project__tags span {
  padding: 5px 10px;
  border-radius: 5px;
  font-weight: 700;
  background: transparentize($color-gray, 0.85);
}

.modal-project__text a {
  font-weight: 700;
  color: $color-neon-blue;
  text-decoration: underline;
  text-underline-offset: 4px;

  &:hover {
    color: transparentize($color-neon-blue, 0.3);
    cursor: pointer;
  }

  &:active {
    color: transparentize($color-neon-blue, 0.7);
  }
}

.modal-project__link-group {
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 15px;
}

.modal-project__link {
  flex-shrink: 0;
  display: flex;
  gap: 5px;
  padding: 2px 8px 6px;
  border-radius: 10px;
  color: $color-orange;
  text-decoration: underline;
  text-underline-offset: 4px;
  background: $color-gray;

  &:hover {
    border-color: $color-default-white;
    color: $color-default-white;
    cursor: pointer;
  }

  &:active {
    color: transparentize($color-default-white, 0.7);
  }
}

.modal-project__link-icon {
  margin-top: 4px;
  fill: currentColor;
}
