
.main-nav__toggle {
  position: relative;
  z-index: 10;
  display: block;
  width: 54px;
  height: 54px;
  border: 0;
  border-radius: 6px;
  background-color: transparent;
  cursor: pointer;

  &::before {
    transform: translateY(-8px);
    content: "";
  }

  &::after {
    transform: translateY(8px);
    content: "";
  }
}

.main-nav__toggle::before,
.main-nav__toggle span,
.main-nav__toggle::after {
  background-color: $color-default-white;
  border-radius: 6px;
  height: 2px;
  position: absolute;
  left: calc(50% - 15px);
  top: calc(50% - 1px);
  transition: all 0.3s ease-out;
  width: 30px;
}

.main-nav__toggle--open {
  span {
    background-color: transparent;
    transform: translateX(-70%);
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.main-nav__menu {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 100%;
  width: 100%;
  height: calc(100vh - 100%);
  padding: 20px 20px 30px;
  pointer-events: none;
  transform: translate(100%, 0);
  background-color: $color-accent-main;
  //transition: all 0.5s cubic-bezier(0.77, 0.2, 0.05, 1.0);

  @media screen and (min-width: 1280px) {
    align-items: center;
    background-color: transparent;
    width: auto;
    height: auto;
    opacity: 0;
    padding: 0;
    pointer-events: all;
    right: 30px;
    transform: translateY(-50%);
    top: 50%;
    transition: none;
  }
}

.main-nav__menu--open {
  pointer-events: all;
  right: 0;
  transform: translate(0, 0);
  z-index: 1;
}

.main-nav__menu.show {
  @media screen and (min-width: 1280px) {
    animation: menu-to-left 0.5s forwards 0.6s;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.main-nav__item {
  align-items: center;
  border-bottom: 1px solid $color-gray;
  display: flex;
  justify-content: flex-start;
  opacity: 0;
  transform: translateX(100%);
  transition: 0.5s;

  @media screen and (min-width: 1280px) {
    background-color: $color-accent-main;
    border: none;
    border-radius: 50%;
    height: 50px;
    opacity: 1;
    width: 50px;

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    &:hover {
      background-color: $color-orange;

      .main-nav__link {
        span {
          background-color: $color-orange;
          border-radius: 30px 0 0 30px;
          opacity: 1;
          pointer-events: all;
          right: 27px;
        }

        svg {
          fill: $color-default-white;
        }
      }
    }
  }

  &--active {
    color: $color-orange;

    @media screen and (min-width: 1280px) {
      background-color: $color-orange;
    }

    .main-nav__link {
      color: $color-orange;

      @media screen and (min-width: 1280px) {
        color: $color-default-white;
      }
    }

    .main-nav__icon {
      fill: $color-orange;

      @media screen and (min-width: 1280px) {
        fill: $color-default-white;
      }
    }
  }

  &--open {
    transform: translateX(0);
    opacity: 1;
  }
}

.main-nav__link {
  color: $color-default-white;
  display: flex;
  align-items: center;
  padding: 10px 0;
  text-decoration: none;
  transition: 0.2s;
  width: 100%;

  @media screen and (min-width: 1280px) {
    align-items: center;
    display: flex;
    justify-content: center;
    height: 50px;
    padding: 0;
    position: relative;
    width: 50px;
  }
}

.main-nav__link span {
  font-family: $ff-default;
  font-size: 18px;
  font-weight: 600;
  line-height: 30px;
  padding-left: 25px;
  text-transform: uppercase;

  @media screen and (min-width: 1280px) {
    border-radius: 30px;
    height: 50px;
    line-height: 50px;
    opacity: 0;
    padding: 0 30px 0 20px;
    position: absolute;
    pointer-events: none;
    right: 0;
    transition: all ease-out 0.2s;
    white-space: nowrap;
    z-index: -1;
  }
}

.main-nav__icon {
  fill: $color-orange;

  @media screen and (min-width: 1280px) {
    transition: 0.2s;
    fill: $color-default-white;
    z-index: 2;
  }
}

.main-nav__social {
  margin-top: auto;
  transform: translateY(150%);
  opacity: 0;
  transition: 0.5s;
}

.main-nav__social--open {
  transform: translateY(0);
  opacity: 1;
}
