.social {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.social__link {
  color: $color-orange;

  &:hover {
    color: $color-default-white;
    cursor: pointer;
  }

  &:active {
    color: transparentize($color-default-white, 0.7);
  }
}

.social__icon {
  display: block;
  width: 24px;
  height: 24px;
  color: inherit;
}

.social--theme-white .social__link {
  color: $color-default-white;

  &:hover {
    color: $color-orange;
  }

  &:active {
    color: transparentize($color-orange, 0.7);
  }
}
