.header {
  padding: 10px 0;
  background-color: $color-accent-main;

  @include vp-768 {
    border-bottom: 1px solid transparentize(#ffb400, 0.5);
  }
}

.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  font-weight: 700;
  color: $color-default-white;
}

.header--fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}

.header__toggle {
  position: relative;
  z-index: 10;
  display: block;
  width: 54px;
  height: 54px;
  border: 0;
  border-radius: 6px;
  background-color: transparent;
  cursor: pointer;

  @include vp-768 {
    display: none;
  }

  &::before {
    transform: translateY(-8px);
    content: "";
  }

  &::after {
    transform: translateY(8px);
    content: "";
  }
}

.header__toggle::before,
.header__toggle span,
.header__toggle::after {
  background-color: $color-default-white;
  border-radius: 6px;
  height: 2px;
  position: absolute;
  left: calc(50% - 15px);
  top: calc(50% - 1px);
  transition: all 0.3s ease-out;
  width: 30px;
}

.header__toggle--open {
  span {
    background-color: transparent;
    transform: translateX(-70%);
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.header__menu {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 100%;
  width: 100%;
  height: calc(100vh - 100%);
  padding: 20px 20px 30px;
  pointer-events: none;
  transform: translate(100%, 0);
  background-color: $color-accent-main;
  //transition: all 0.5s cubic-bezier(0.77, 0.2, 0.05, 1.0);

  @include vp-768 {
    position: static;
    flex-direction: row;
    align-items: center;
    width: auto;
    height: auto;
    padding: 0;
    transform: none;
    pointer-events: all;
  }
}

.header__menu--open {
  pointer-events: all;
  right: 0;
  transform: translate(0, 0);
  z-index: 1;
}

.header__list {
  margin: 0;
  padding: 0;
  list-style: none;

  @include vp-768 {
    display: flex;
    gap: 24px;
  }
}

.header__list-item {
  align-items: center;
  border-bottom: 1px solid $color-gray;
  display: flex;
  justify-content: flex-start;
  opacity: 0;
  transform: translateX(100%);
  transition: 0.5s;

  @include vp-768 {
    position: relative;
    border: none;
    transform: none;
    opacity: 1;

    &::after {
      position: absolute;
      top: 50%;
      right: -12px;
      width: 4px;
      height: 4px;
      transform: rotate(45deg) translateY(-50%);
      transform-origin: 50%, 50%;
      background-color: $color-default-white;
      content: "";
    }

    &:last-child::after {
      display: none;
    }
  }

  &--active {
    color: $color-orange;

    .header__link {
      color: $color-orange;
    }

    .header__icon {
      fill: $color-orange;
    }
  }

  &--open {
    transform: translateX(0);
    opacity: 1;
  }
}

.header__link {
  color: $color-default-white;
  display: flex;
  align-items: center;
  padding: 10px 0;
  text-decoration: none;
  width: 100%;

  @include vp-768 {
    padding: 1px 0 0;
    border-bottom: 1px solid transparent;

    &:hover {
      border-color: $color-orange;
      color: $color-orange;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

.header__link span {
  font-family: $ff-default;
  font-size: 18px;
  font-weight: 600;
  line-height: 30px;
  padding-left: 25px;
  text-transform: uppercase;

  @include vp-768 {
    padding-left: 0;
  }
}

.header__icon {
  fill: $color-orange;

  @include vp-768 {
    display: none;
  }
}

.header__social {
  justify-content: center;
  margin-top: auto;
  transform: translateY(150%);
  opacity: 0;
  transition: 0.5s;
  color: $color-orange;

  @include vp-768 {
    margin-top: 0;
    margin-left: 24px;
    padding-left: 24px;
    border-left: 1px solid transparentize($color-orange, 0.5);
    transform: none;
    opacity: 1;
    color: $color-default-white;
  }
}

.header__social--open {
  transform: translateY(0);
  opacity: 1;
}
