.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal, transform $trans-modal, visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 28px 15px;
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 100%;
    max-width: 1190px;

    color: $color-default-black;
    background-color: $color-default-white;
  }

  &__close-btn {
    position: absolute;
    top: 3px;
    right: 3px;
    z-index: 1;

    display: flex;

    width: 40px;
    height: 40px;
    padding: 0;

    background: $color-transparent;
    border: none;
    color: $color-orange;
    cursor: pointer;

    transition: opacity $trans-default;

    @include hover-focus {
      opacity: 0.5;
    }
  }

  &__close-btn-icon {
    width: 30px;
    height: 30px;
    margin: auto;
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include vp-768 {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    transition: none;
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }
}

.modal__header {
  position: sticky;
  top: 0;
  padding: 8px 15px 12px;
  background-color: $color-gray;
  color: $color-orange;
}

.modal__body {
  padding: 14px 15px 15px;
}

.modal__heading {
  margin-bottom: 0;
  padding-right: 30px;
  text-align: left;
}
