.contacts {
  margin-bottom: 40px;
}

.contacts__entry {
  margin-bottom: 20px;
}

.contacts__icon {
  color: $color-orange;
}

.contacts__link {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  color: $color-default-white;

  &:hover .contacts__icon,
  &:active .contacts__icon {
    color: $color-default-white;
  }

  &:hover {
    text-decoration: underline;
    text-underline-offset: 4px;
  }

  &:active {
    opacity: 0.3;
  }
}

.contacts__social {
  padding-left: 1px;
  color: $color-orange;
}
