.projects {
  margin-bottom: 40px;
  overflow: hidden;
}

.projects__wrapper {
  height: auto;
  margin: 0 auto;
  padding: 0 15px;
}

.projects__list {
  align-items: stretch;
  max-width: 290px;
  margin: 0 auto 20px;
  padding: 0;

  @include vp-640 {
    max-width: 100%;
  }
}

.projects__item {
  //flex-shrink: 0;
  display: flex;
  flex-direction: column;
  max-width: 290px;
  height: auto;
  border: 2px solid var(--orange, $color-orange);
  border-radius: 15px;
  background-color: $color-accent-main;
}

.projects__picture {
  display: flex;
  justify-content: center;
  align-items: center;
  order: -1;
  min-height: 213px;
  padding: 10px;
  border-top-left-radius: 13px;
  border-top-right-radius: 13px;
  background:
    linear-gradient(180deg, #d9d9d9 0%, rgba(0, 0, 0, 0) 95%),
    linear-gradient($color-default-black, $color-default-black);
}

.projects__image {
  display: block;
  object-fit: contain;
}

.projects__info {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.projects__title {
  margin: 0;
  margin-bottom: 10px;
  font: 700 18px/21px $ff-default;
  text-align: center;
  color: $color-orange;
}

.projects__description {
  width: 80%;
  margin: 0 auto 25px;
  text-align: center;
}

.projects__tags {
  margin-bottom: 25px;
  color: $color-orange;
}

.projects__button {
  display: flex;
  gap: 3px;
  align-items: center;
  margin-top: auto;
  margin-left: auto;
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;

  &::after {
    width: 13px;
    height: 13px;
    border-right: 2px solid $color-default-white;
    border-bottom: 2px solid $color-default-white;
    transform: rotate(-45deg);
    content: "";
  }

  &:hover {
    text-decoration: underline;
    text-underline-offset: 6px;
    text-shadow:
      1px 1px 2px $color-orange,
      0 0 2px $color-orange,
      0 0 2px $color-orange;

    &:active {
      opacity: 0.4;
    }

    &::after {
      border-color: transparentize($color-orange, 0.4);
    }
  }
}

.projects__controls {
  display: flex;
  gap: 15px;
  justify-content: space-between;
  align-items: center;
}

.projects__toggle {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  margin: 0;
  padding: 0;
  border: 2px solid $color-orange;
  border-radius: 50%;
  background: $color-default-black;
  color: $color-orange;

  &:hover {
    border-color: $color-default-white;
    color: $color-default-white;
    cursor: pointer;
  }

  &:active {
    opacity: 0.4;
  }

  &--prev {
    left: -14px;

    & .projects__icon {
      transform: translateX(-15%);
    }
  }

  &--next {
    right: -14px;

    & .projects__icon {
      transform: translateX(15%) scaleX(-1);
    }
  }
}

.projects__icon {
  transform-origin: 50% 50%;
}

.projects__toggle--disable {
  pointer-events: none;
  opacity: 0.1;
}

.projects__pagination {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
  justify-content: center;
  align-items: center;
}

.projects__bullet {
  display: block;
  width: 10px;
  height: 10px;
  padding: 1px;
  border: 1px solid $color-orange;
  border-radius: 2px;
  background-color: $color-default-white;
  background-clip: content-box;
}

.projects__bullet--active {
  background-color: $color-orange;
}
